export default {
	data() {
		return {
      hasPayPwd: false
		}
	},
	methods: {
    hasPayPassword() {
      const that = this
      this.$http({
        aurl: 'hasPayPassword',
        success: res => {
          if(res.data.code === 0){
            const result = res.data.result
            that.hasPayPwd = result.status
            if(result.status == 0){ // 
              uni.setStorageSync('accountStatus', result.status);
              uni.showModal({
              	title: '',
              	content: '',
              	cancelText: '',
              	confirmText: '',
              	confirmColor: '#5C6F97',
              	success (res) {
              		if (res.confirm) {
              			// console.log('')
                    that.needTokenQueryLink('/pagesA/user/wallet/authentication', {use_type: 'set'});
              		} else if (res.cancel) {
              			// console.log('')
              			return false;
              		}
              	}
              })
            } else if (result.status == 1 || result.status == 2){ // 
              that.toQueryLink('/pagesA/user/wallet/index',{accountStatus: result.status});
            }
          }
        },
        fail: error => {
          // uni.navigateTo({
          // 	url:"/pagesA/user/bindUser"
          // })
          console.log(error);
        }
      });
    },
	},
	onLoad: function(options) {

	},
	mounted() {}
};
